import React from 'react';
import { Navigate } from 'react-router-dom';

// Admin Authentication
export const adminAuth = {
  // Giri kontrol
  login: (username, password) => {
    // Gvenli giri bilgileri (production'da environment variables kullann)
    const validCredentials = {
      username: 'admin',
      password: 'admin123'
    };

    if (username === validCredentials.username && password === validCredentials.password) {
      const sessionData = {
        username: username,
        loginTime: Date.now(),
        isAdmin: true
      };
      localStorage.setItem('adminSession', JSON.stringify(sessionData));
      return {
        success: true,
        message: 'Giri baarl'
      };
    }
    return {
      success: false,
      message: 'Kullanc ad veya ifre hatal'
    };
  },

  // k
  logout: () => {
    localStorage.removeItem('adminSession');
  },

  // Giri durumu kontrol
  isLoggedIn: () => {
    const session = localStorage.getItem('adminSession');
    if (!session) return false;
    
    try {
      const sessionData = JSON.parse(session);
      // Session'n 24 saat geerli olup olmadn kontrol et
      const now = Date.now();
      const sessionAge = now - sessionData.loginTime;
      const maxAge = 24 * 60 * 60 * 1000; // 24 saat
      
      if (sessionAge > maxAge) {
        localStorage.removeItem('adminSession');
        return false;
      }
      
      return true;
    } catch (error) {
      localStorage.removeItem('adminSession');
      return false;
    }
  },

  // Kullanc bilgilerini al
  getUser: () => {
    const session = localStorage.getItem('adminSession');
    if (!session) return null;
    
    try {
      return JSON.parse(session);
    } catch (error) {
      return null;
    }
  }
};

// Protected Route bileeni
export const ProtectedRoute = ({ children }) => {
  const isAuthenticated = adminAuth.isLoggedIn();
  
  if (!isAuthenticated) {
    return <Navigate to="/admin/login" replace />;
  }
  
  return children;
}; 