// Blog yazlarn kategoriye gre filtreleme
export const filterBlogPostsByCategory = (category) => {
  if (category === "Tm") {
    return blogPosts;
  }
  return blogPosts.filter(post => post.category === category);
};

// ne kan blog yazlarn getirme
export const getFeaturedBlogPosts = () => {
  return blogPosts.filter(post => post.featured);
};

// Blog yazlarn tarihe gre sralama (en yeni nce)
export const getSortedBlogPosts = () => {
  return [...blogPosts].sort((a, b) => new Date(b.publishDate) - new Date(a.publishDate));
};

// Blog yazsn ID'ye gre bulma
export const getBlogPostById = (id) => {
  return blogPosts.find(post => post.id === parseInt(id));
}; 